<template>
    <div class="foodWrap">
        <header>
            <router-link to="/msite"><i class="el-icon-arrow-left"></i> </router-link>
            <div class="top_title1"><span>{{foodTitle}}</span></div>
        </header>
        <section class="sort_container">
            <div class="sort_item" @click="getShopMsg" >
                <div class="sort_item_container">
                    <div class="sort_item_border" v-if="showflbtn">
                        <span>{{foodTitle}}</span>
                        <i class="el-icon-caret-bottom"></i>
                    </div>
                    <div class="sort_item_border textcolor" v-if="!showflbtn">
                        <span>分类</span>
                        <i class="el-icon-caret-top"></i>
                    </div>
                </div>
            </div>
            <div class="sort_item" @click="getpaixu">
                <div class="sort_item_container">
                    <div class="sort_item_border" v-if="showflbtn1">
                        <span>排序</span>
                        <i class="el-icon-caret-bottom"></i>
                    </div>
                    <div class="sort_item_border textcolor" v-if="!showflbtn1">
                        <span>排序</span>
                        <i class="el-icon-caret-top"></i>
                    </div>
                </div>
            </div>
            <div class="sort_item" @click="getshaixuan">
                <div class="sort_item_container">
                    <div class="sort_item_border" v-if="showflbtn2">
                        <span>筛选</span>
                        <i class="el-icon-caret-bottom"></i>
                    </div>
                    <div class="sort_item_border textcolor" v-if="!showflbtn2">
                        <span>筛选</span>
                        <i class="el-icon-caret-top"></i>
                    </div>
                </div>
            </div>
        </section>
        <el-collapse-transition>
            <section class="hidden_block" v-if="isShow">
                <div class="nav_left">
                    <ul>
                        <li v-for="(fl,index) in shopfeilei" :key="index" @click="getShopFeilei(index)" :class="num == index ? 'activity_menu' : '' ">
                            <div class="nav_title":style="bgcolor">
                                <i class="el-icon-burger"></i>
                                <span>{{fl.name}}</span>
                            </div>
                            <div class="nav_icon">
                                <span>{{fl.count}}</span>
                                <i class="el-icon-arrow-right"></i>
                            </div>
                        </li>
                    </ul>
                </div>
                <div class="nav_right">
                    <ul>
                        <li v-for="(flist,index) in feileilist" :key="index" @click="getfeileilist(flist)">
                            <span>{{flist.name}}</span>
                            <span>{{flist.count}}</span>
                        </li>
                    </ul>
                </div>
            </section>
        </el-collapse-transition>
        <el-collapse-transition>
            <section class="hidden_block1" v-if="isShowpaixu">
                <div class="paixu">
                    <ul>
                        <li @click="getpaixulist(4)">
                            <i class="el-icon-sort"></i>
                            <p>智能排序</p>
                        </li>
                        <li @click="getpaixulist(5)">
                            <i class="el-icon-location-outline"></i>
                            <p>距离最近</p>
                        </li>
                        <li @click="getpaixulist(6)">
                            <i class="el-icon-medal-1"></i>
                            <p>销量最高</p>
                        </li>
                        <li @click="getpaixulist(1)">
                            <i class="el-icon-sort"></i>
                            <p>起送价最低</p>
                        </li>
                        <li @click="getpaixulist(2)">
                            <i class="el-icon-sort"></i>
                            <p>配送速度最快</p>
                        </li>
                        <li @click="getpaixulist(3)">
                            <i class="el-icon-sort"></i>
                            <p>评价最高</p>
                        </li>

                    </ul>
                </div>
            </section>
        </el-collapse-transition>
        <el-collapse-transition>
            <section class="hidden_block1" v-if="isShowshaixuan">
                <div class="sort_detail_type">
                    <div class="detail_type" id="detail_type1">
                        <div>配送方式</div>
                        <ul>
                            <li @click="chooseChecked(10)">
                                <i class="el-icon-info" style="display: block;"></i>
                                <i class="el-icon-check" style="display: none;"></i>
                                <span>蜂鸟专送</span>
                            </li>
                        </ul>
                    </div>
                    <div class="detail_type" id="detail_type">
                        <div>商家属性（可以多选）</div>
                        <ul>
                            <li v-for="(sx,index) in shopsx" @click="chooseChecked(index+1)">
                                <div class="filter_icon" :style="'color:#'+sx.icon_color+';'+'border-color:#'+sx.icon_color+';'" style="display: block;">{{sx.icon_name}}</div>
                                <i class="el-icon-check" style="display: none;"></i>
                                <span>{{sx.name}}</span>
                            </li>

                        </ul>
                    </div>
                    <footer>
                        <div class="clear_msg">清空</div>
                        <div class="sure_msg" @click="selectSx">确定<span>({{chooseCount}})</span></div>
                    </footer>
                </div>
            </section>
        </el-collapse-transition>
        <div class="content">
            <div class="showShop">
                <ul>
                    <li v-for="lis in shoplists" :key="lis.id" @click="setShopId(lis)">
                        <section>
                            <img :src="imgurl+lis.image_path" alt="" class="shop_img">
                        </section>
                        <div class="shop_content">
                            <div class="top_title">
                                <h4>
                                    <span class="y_block">品牌</span>
                                    <span>{{lis.name}}</span>
                                </h4>
                                <ul>
                                    <li>保</li>
                                    <li>准</li>
                                    <li>票</li>
                                </ul>
                            </div>

                            <div class="middle_content">
                                <div class="star">
                                    <i class="el-icon-star-on"></i>
                                    <i class="el-icon-star-on"></i>
                                    <i class="el-icon-star-on"></i>
                                    <i class="el-icon-star-on"></i>
                                    <i class="el-icon-star-on"></i>
                                </div>
                                <span>4.7</span>
                                <span>月销售85单</span>
                                <div class="ratingRight">
                                    <span>蜂鸟专送</span>
                                    <span>准时达</span>
                                </div>

                            </div>
                            <div class="postmsg">
                                <p>
                                    <span>¥{{lis.float_delivery_fee}}起送</span>
                                    <span>/</span>
                                    <span>{{lis.piecewise_agent_fee.tips}}</span>
                                </p>
                                <p>
                                    <span>{{lis.distance}}</span>
                                    <span>/</span>
                                    <span>{{lis.order_lead_time}}</span>
                                </p>
                            </div>
                        </div>
                    </li>
                </ul>
            </div>
        </div>

    </div>
</template>

<script>
    export default {
        name: "Food",
        data(){
            return{
                isShow:false,
                isShowpaixu:false,
                isShowshaixuan:false,
                checkedDG: true,
                latitude:"",
                longitude:"",
                shoplists:[],
                shopfeilei:[],
                feileilist:[],
                bgcolor:'',
                shopsx:[],
                foodTitle:'',
                supportArr:[],
                // checkCount:1,
                num:0,
                showflbtn:true,
                showflbtn1:true,
                showflbtn2:true,
                imgurl:"https://elm.cangdu.org/img/",
                chooseCheckedNum1:"",
                chooseCheckedNum2:"",
                chooseCheckedNum3:"",
                chooseCheckedNum4:"",
                chooseCheckedNum5:"",
                chooseCheckedNum6:"",
                chooseCheckedNum7:"",

                checkCount1:0,
                checkCount2:0,
                checkCount3:0,
                checkCount4:0,
                checkCount5:0,
                checkCount6:0,
                checkCount7:0,
                chooseCount:0

            }
        },
        created(){
            this.checkCount1 = 1;
            this.checkCount2 = 1;
            this.checkCount3 = 1;
            this.checkCount4 = 1;
            this.checkCount5 = 1;
            this.checkCount6 = 1;
            this.checkCount7 = 1;
            this.chooseCount == 0;

            this.foodTitle = localStorage.getItem("foodTitle");
            let arr = JSON.parse(localStorage.getItem("placeHistory"));
            this.latitude = arr[0].latitude;
            this.longitude = arr[0].longitude;
            this.axios.get("/shopping/restaurants", {
                params: {latitude:this.latitude ,longitude:this.longitude}
            }).then(res=>{
                this.shoplists = res;
            }).catch(err=>{});
        },
        methods:{
            getShopMsg(){
                if(this.isShowshaixuan == true){
                    this.isShowshaixuan = false;
                }
                if(this.isShowpaixu == true){
                    this.isShowpaixu = false;
                }
                this.showflbtn = !this.showflbtn;
                this.isShow = !this.isShow;
                this.axios.get("/shopping/v2/restaurant/category", {
                    params: {latitude:this.latitude ,longitude:this.longitude}
                }).then(res=>{
                    this.shopfeilei = res;
                    this.feileilist = this.shopfeilei[0].sub_categories;
                }).catch(err=>{});
            },
            getShopFeilei(n){
                this.num = n;
                this.feileilist = this.shopfeilei[n].sub_categories;
            },
            getfeileilist(num){
                this.isShow = !this.isShow;
                this.foodTitle = num.name;
                this.axios.get("shopping/restaurants?latitude="+this.latitude+'&longitude='+this.longitude+'&restaurant_category_ids[]='+num.id).then(res=>{
                    this.shoplists = res;
                }).catch(err=>{});
            },
            getpaixu(){
                if(this.isShowshaixuan == true){
                    this.isShowshaixuan = false;
                }
                if(this.isShow == true){
                    this.isShow = false;
                }
                this.showflbtn1 = !this.showflbtn1;
                this.isShowpaixu = !this.isShowpaixu;
            },
            getshaixuan(){
                if(this.isShowpaixu == true){
                    this.isShowpaixu = false;
                }
                if(this.isShow == true){
                    this.isShow = false;
                }
                this.showflbtn2 = !this.showflbtn2;
                this.isShowshaixuan = !this.isShowshaixuan;
                this.axios.get("shopping/v1/restaurants/activity_attributes").then(res=>{
                    this.shopsx = res;
                    this.supportArr = res.map(function (v) {
                       return v.id;
                    });
                    console.log(this.supportArr);
                }).catch(err=>{});
            },
            getpaixulist(number){
                this.isShowpaixu = !this.isShowpaixu;
                this.axios.get("/shopping/restaurants", {
                    params: {latitude:this.latitude ,longitude:this.longitude,order_by:number}
                }).then(res=>{
                    this.shoplists = res;
                    // console.log(res);
                }).catch(err=>{});
            },
            chooseChecked(checknum){
                console.log(checknum)
                if(checknum == 1){
                    if(this.checkCount1 %2){
                        let selector = "#detail_type>ul>li:nth-child("+checknum+")>div";
                        document.querySelector(selector).style.display = "none"
                        let selector1 = "#detail_type>ul>li:nth-child("+checknum+")>i";
                        document.querySelector(selector1).style.display = "block"
                        this.checkCount1 = 2;
                        this.chooseCount++;
                    }else{
                        let selector = "#detail_type>ul>li:nth-child("+checknum+")>div";
                        document.querySelector(selector).style.display = "block"
                        let selector1 = "#detail_type>ul>li:nth-child("+checknum+")>i";
                        document.querySelector(selector1).style.display = "none"
                        this.checkCount1 = 1;
                        this.chooseCount--;
                    }
                    this.chooseCheckedNum1 = 8
                }else if(checknum == 2){
                    if(this.checkCount2 %2){
                        let selector = "#detail_type>ul>li:nth-child("+checknum+")>div";
                        document.querySelector(selector).style.display = "none"
                        let selector1 = "#detail_type>ul>li:nth-child("+checknum+")>i";
                        document.querySelector(selector1).style.display = "block"
                        this.checkCount2 = 2;
                        this.chooseCount++;
                    }else{
                        let selector = "#detail_type>ul>li:nth-child("+checknum+")>div";
                        document.querySelector(selector).style.display = "block"
                        let selector1 = "#detail_type>ul>li:nth-child("+checknum+")>i";
                        document.querySelector(selector1).style.display = "none"
                        this.checkCount2 = 1;
                        this.chooseCount--;
                    }
                    this.chooseCheckedNum2 = 7
                } else if(checknum == 3){
                    if(this.checkCount3 %2){
                        let selector = "#detail_type>ul>li:nth-child("+checknum+")>div";
                        document.querySelector(selector).style.display = "none"
                        let selector1 = "#detail_type>ul>li:nth-child("+checknum+")>i";
                        document.querySelector(selector1).style.display = "block"
                        this.checkCount3 = 2;
                        this.chooseCount++;
                    }else{
                        let selector = "#detail_type>ul>li:nth-child("+checknum+")>div";
                        document.querySelector(selector).style.display = "block"
                        let selector1 = "#detail_type>ul>li:nth-child("+checknum+")>i";
                        document.querySelector(selector1).style.display = "none"
                        this.checkCount3 = 1;
                        this.chooseCount--;
                    }
                    this.chooseCheckedNum3 = 9
                } else if(checknum == 4){
                    if(this.checkCount4 %2){
                        let selector = "#detail_type>ul>li:nth-child("+checknum+")>div";
                        document.querySelector(selector).style.display = "none"
                        let selector1 = "#detail_type>ul>li:nth-child("+checknum+")>i";
                        document.querySelector(selector1).style.display = "block"
                        this.checkCount4 = 2;
                        this.chooseCount++;
                    }else{
                        let selector = "#detail_type>ul>li:nth-child("+checknum+")>div";
                        document.querySelector(selector).style.display = "block"
                        let selector1 = "#detail_type>ul>li:nth-child("+checknum+")>i";
                        document.querySelector(selector1).style.display = "none"
                        this.checkCount4 = 1;
                        this.chooseCount--;
                    }
                    this.chooseCheckedNum4 = 5
                } else if(checknum == 5){
                    if(this.checkCount5 %2){
                        let selector = "#detail_type>ul>li:nth-child("+checknum+")>div";
                        document.querySelector(selector).style.display = "none"
                        let selector1 = "#detail_type>ul>li:nth-child("+checknum+")>i";
                        document.querySelector(selector1).style.display = "block"
                        this.checkCount5 = 2;
                        this.chooseCount++;
                    }else{
                        let selector = "#detail_type>ul>li:nth-child("+checknum+")>div";
                        document.querySelector(selector).style.display = "block"
                        let selector1 = "#detail_type>ul>li:nth-child("+checknum+")>i";
                        document.querySelector(selector1).style.display = "none"
                        this.checkCount5 = 1;
                        this.chooseCount--;
                    }
                    this.chooseCheckedNum5 = 3
                } else if(checknum == 6){
                    if(this.checkCount6 %2){
                        let selector = "#detail_type>ul>li:nth-child("+checknum+")>div";
                        document.querySelector(selector).style.display = "none"
                        let selector1 = "#detail_type>ul>li:nth-child("+checknum+")>i";
                        document.querySelector(selector1).style.display = "block"
                        this.checkCount6 = 2;
                        this.chooseCount++;
                    }else{
                        let selector = "#detail_type>ul>li:nth-child("+checknum+")>div";
                        document.querySelector(selector).style.display = "block"
                        let selector1 = "#detail_type>ul>li:nth-child("+checknum+")>i";
                        document.querySelector(selector1).style.display = "none"
                        this.checkCount6 = 1;
                        this.chooseCount--;
                    }
                    this.chooseCheckedNum6 = 4
                }else if( checknum == 10){
                    if(this.checkCount7 %2){
                        let selector = "#detail_type1>ul>li>i:nth-child(1)";
                        document.querySelector(selector).style.display = "none"
                        let selector1 = "#detail_type1>ul>li>i:nth-child(2)";
                        document.querySelector(selector1).style.display = "block"
                        this.checkCount7 = 2;
                        this.chooseCount++;
                    }else{
                        let selector = "#detail_type1>ul>li>i:nth-child(1)";
                        document.querySelector(selector).style.display = "block"
                        let selector1 = "#detail_type1>ul>li>i:nth-child(2)";
                        document.querySelector(selector1).style.display = "none"
                        this.checkCount7 = 1;
                        this.chooseCount--;
                    }
                    this.chooseCheckedNum7 = 1
                }
            },
            selectSx(){
                this.axios.get("shopping/restaurants?latitude="+this.latitude+'&longitude='+this.longitude+'&delivery_mode[]='+this.chooseCheckedNum7+'&support_ids[]='+this.chooseCheckedNum1+'&support_ids[]='+this.chooseCheckedNum2+'&support_ids[]='+this.chooseCheckedNum3+'&support_ids[]='+this.chooseCheckedNum4+'&support_ids[]='+this.chooseCheckedNum5+'&support_ids[]='+this.chooseCheckedNum6).then(res=>{
                    console.log(res);
                    this.shoplists = res
                    this.isShowshaixuan = false
                }).catch(err=>{})
            },
            setShopId(ShopId){
                localStorage.setItem("shopId",JSON.stringify(ShopId));
                this.$router.push("/shop");
            }

        }
    }
</script>
<style>
