        width: 100%;
        position: fixed;
        display: flex;
        top: 3.6rem;
        left: 0;
        border-top: .025rem solid #e4e4e4;
        background-color: #fff;
        z-index: 200;
    }
    .nav_left{
        background-color: #f1f1f1;
        height: 16rem;
        width: 50%;
        float: left;
    }
    .nav_left>ul:hover{
        outline: none;
    }
    .nav_left>ul>li{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 .5rem;
        line-height: 1.6rem;
    }
    .activity_menu{
        background-color:#fff;
    }
    /*.activity_menu span{*/
        /*font-weight:700;*/
    /*}*/
    .nav_title>i{
        font-size: .5rem;
        margin-right: .2rem;
        padding-top: .2rem;
    }
    .nav_title>span{
        font-size: .5rem;
        color: #666;
        line-height: 1.6rem;
    }
    .nav_icon>span{
        background-color: #ccc;
        font-size: .4rem;
        color: #fff;
        padding: 0 .1rem;
        border: .025rem solid #ccc;
        border-radius: .8rem;
        vertical-align: middle;
        margin-right: .25rem;
        line-height: 1.6rem;
    }
    .nav_icon>i{
        vertical-align: middle;
        font-size: .5rem;
    }
    .nav_right{
        background-color: #fff;
        height: 16rem;
        width: 50%;
        float: left;
        overflow: scroll;
    }
    .nav_right>ul{
        width: 95%;
        float: right;

    }
    .nav_right>ul>li{
        display: flex;
        justify-content: space-between;
        height: 1.7rem;
        line-height: 1.7rem;
        padding-right: .5rem;
        border-bottom: .025rem solid #e4e4e4;
    }
    .nav_right>ul>li>span{
        color: #666;
        font-size: .55rem;
    }
    .hidden_block1{
        width: 100%;
        position: fixed;
        display: flex;
        top: 3.6rem;
        left: 0;
        border-top: .025rem solid #e4e4e4;
        background-color: #fff;
        z-index: 200;
    }
    .paixu{
        width: 100%;
    }
    .paixu>ul>li{
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .paixu>ul>li>i{
        font-size: .65rem;
        margin: 0 .3rem 0 .8rem;
        color: #666;
    }
    .paixu>ul>li>p{
        line-height: 2.5rem;
        flex: auto;
        text-align: left;
        text-indent: .25rem;
        font-size: .55rem;
        border-bottom: .025rem solid #e4e4e4;
        display: flex;
        justify-content: space-between;
        align-items: center;
        color: #666;
    }

    .sort_detail_type{
        flex-direction: column;
        align-items: flex-start;
        min-height: 10.6rem;
        background-color: #f1f1f1;
    }
    .detail_type{
        width: 100%;
    }
    .detail_type>div{
        font-size: .4rem;
        color: #333;
        line-height: 1.5rem;
        height: 1.5rem;
        text-align: left;
        padding-left: .5rem;
        background-color: #fff;
    }
    .detail_type>ul{
        display: flex;
        flex-wrap: wrap;
        padding: 0 .5rem;
        background-color: #fff;
    }
    .detail_type>ul>li{
        display: flex;
        align-items: center;
        border: .025rem solid #eee;
        box-sizing: border-box;
        width: 4.7rem;
        height: 1.4rem;
        margin-right: .25rem;
        border-radius: .125rem;
        padding: 0 .25rem;
        margin-bottom: .25rem;
    }
    /*.detail_type>ul>li>i{*/
        /*font-size: .8rem;*/
        /*margin-right: .125rem;*/
    /*}*/
    .detail_type>ul>li>.filter_icon{
        width: .8rem;
        height: .8rem;
        font-size: .5rem;
        border: .025rem solid #e4e4e4;
        border-radius: .15rem;
        margin-right: .25rem;
        line-height: .8rem;
        text-align: center;
    }
    .detail_type>ul>li>span{
        font-size: .4rem;
        color: #333;
    }
    footer{
        display: flex;
        background-color: #f1f1f1;
        width: 100%;
        box-sizing: border-box;
        padding: .3rem .2rem;
    }

    footer>div{
        width: 50%;
        height: 1.8rem;
        font-size: .8rem;
        line-height: 1.8rem;
        border-radius: .2rem;
        text-align: center;
    }
    .clear_msg{
        background-color: #fff;
        margin-right: .5rem;
        border: .025rem solid #fff;
    }
    .sure_msg{
        background-color: #56d176;
        color: #fff;
        border: .025rem solid #56d176;
    }
    .sure_msg>span{
        color: #fff;
